
nav{
    position: fixed;
    width: 100%;
    background-color: var(--light-main);
    height: 90px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    top:900px;
    z-index: 2000;
}
nav p{
    margin: 0px;
    font-size: 10pt;
    font-family: 'Rubik', sans-serif;
    margin-top: 5px;
}
nav ul{
    padding: 10px;
    display: flex;
    flex-direction: row;
    margin: 0px;
    height: 100%;
    align-items: center;
}
nav ul a{
    width: 70px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    outline: none;
    text-decoration: none;
    color: white;
    border-radius: 50%;
    padding: 5px;
    margin: 0px 5px;
}
nav ul a.active{
    background-color: #FE4759;

}
.icon-nav{
    width: 25px;
    max-height: 25px;
    fill: var(--light-sec)
}

.add{
    min-height: 100px;
    min-width: 100px;
    width: 150px;
    height: 150px;
    border-radius:50% ;
    border: 10px solid;
    border-color: var(--light-sec);
    background-color: var(--light-main);
    outline: none;
    cursor: pointer;
    margin-bottom: 90px;
    margin-right: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.add svg{
    fill: var(--light-sec);
    width:50px;
    height: 50px;
}
@media screen and (max-width:600px){
    nav p{
        margin: 0px;
        font-size: 7pt;
        font-family: 'Rubik', sans-serif;
        margin-top: 5px;
    }
    nav ul{
        padding: 10px;

        height: 100%;

    }
    nav ul a{
        width: 50px;
        height: 50px;

    }
    .add{
        width: 100px;
        height: 100px;
        margin-right: 20px;
    }
    .add  svg{
        fill: var(--light-sec);
        width:20px;
        height: 20px;
    }
    nav{
        height: 70px;
    }
}