header{
    width: 100%;
    height: 90px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    top: 0px;
    position: fixed;
    z-index: 3000;
    background-color: var(--light-sec);
}
header .content-icon{
    width: 70px;
    height: 70px;
    fill: var(--light-main);
    cursor: pointer;
}
header .search-container input{
    width: 40px;
    height: 40px;
    border-radius: 40px;
    padding: 20px;
    outline: none;
    border: 3px solid var(--light-main);
    background-color: transparent;
    transition: .5s ease-in-out;
}
header .search-container:hover > input{
    width: 300px;
}
.right-side{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.right-side .search-container{
    position: relative;
    margin-right: 20px;
}
.right-side .search-container button{
    position: absolute;
    background-color: transparent;
    border: none;
    right: 5px;
    top: 10px;
    outline: none;
}
.right-side .search-container .content-icon{
    width: 25px;
    height: 25px;
}
@media screen and (max-width:600px){
    header .search-container:hover > input{
        width: 180px;
    }
    .right-side .search-container .content-icon{
        width: 20px;
        height: 20px;
    }
    .right-side .search-container button{
        right: 7px;
        top: 12px;
    }
    header .content-icon{
        width: 50px;
        height: 50px;
    }
} 