*{
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}
a{
    text-decoration: none;
    border: none;
    outline: none;
    color: black;
}
:root{
    --light-main:#D72638;
    --light-sec:#EEEEEE;
    --dark-main:#1B1B1E;
}
:root,body{
    min-width: 100%;
    min-height: 100%;
    background-color: var(--light-sec);
}
body{
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;
}
#root{
    width: 100%;    
    height: 100%;
}