*{
    margin: 0px;
}
:root{
    --light-main:#D72638;
    --light-sec:#EEEEEE;
    --dark-main:#1B1B1E;
}
.add-form{
    position: relative;
    background-color: var(--light-sec);
    z-index: 3000;
}
.add-form .add-form-content{
    width: 100%;
    height: 100vh;
    padding-top: 70px;
    position: relative;
    z-index: 0;
}
.add-form .title-field{
    padding-left: 20px;
    padding-bottom: 5px;
    width: 100%;
    height: 10vh;
    border: none;
    outline: none;
    background-color: var(--main-light-sec);
    border-bottom: 1px solid var(--light-main);
    font-weight: 300;
    font-family: 'Rowdies', cursive;

}
.add-form .content-field{
    padding: 20px;
    padding-left: 20px;
    width: 100%;
    height: 70vh;
    border: none;
    outline: none;
    background-color: var(--main-light-sec);
    font-family: 'Rowdies', cursive;

}
.add-form .add-controls{
    position: absolute;
    width: 100%;
    top: 0px;
    right: 0px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 10px 10px 0px 10px;
    height: 50px;
}
.add-form .icon-header{
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .5s ease-in-out;
}
.add-form .icon-header svg{
    fill: var(--light-main);
}
.add-form .icon-header:hover{
    background-color: var(--light-main);
}
.add-form .icon-header:hover > svg{
    fill: var(--light-sec);
}
.add-form .right-side{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 220px;
}
.add-form .more-controls{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.add-form .pinned{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 0px 10px;
}
.add-form .pinned label{
    width: 24px;
    height: 24px;
    border-radius: 50%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 0px;
    margin-right: 10px;
}
.add-form .pinned svg{
    fill: var(--dark-main);
}
.add-form .pinned svg.active{
    fill: var(--light-sec);
    background-color: var(--light-main);
    width: 35px;
    height: 35px;
    min-width: 35px;
    max-height: 35px;
    border-radius: 50%;
    padding: 5px;
}
.add-form .more-controls select{
    border: none;
    background-color: var(--light-sec);
    color:var(--dark-main);
    font-family: 'Rowdies', cursive;
    outline: none;
    word-spacing: 4px;
    user-select: none;
    -webkit-user-select: none;
    cursor: pointer;
}