.container{
    margin-top: 90px;
}

.container .header{
    height: 60px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0px 20px;
    margin: 0px;
    padding-bottom: 10px;
    border-bottom: 1px solid var(--dark-main);
}
.container .header span svg{
    cursor: pointer;
    margin-right: 10px;
    fill: var(--dark-main);
}
.container .header h1{
    font-family: 'Noto Sans TC', sans-serif;
}
.container .pinned{
    width: 100%;
    min-width: 100%;
    padding: 0px 20px;
}
.container .other{
    width: 100%;
    min-width: 100%;
    padding: 0px 20px;
}
.container .title-content{
    color: #212529;
    margin : 10px 0px;
}
.container .content{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
}
.container .task{
    min-width: 90px;
    min-height: 90px;
    max-width: 50%;
    max-height: 25vh;
    width: fit-content;
    height: fit-content;
    border-radius: 10px;
    border: 1px solid #adb5bd;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    margin: 10px 0px;
    margin-right: 10px;
    overflow: hidden;
}
.container h3{
    word-wrap: break-word;
    text-overflow: ellipsis;
    max-width: 100%;
}
.container .task-content{
    text-overflow: ellipsis;
    max-width: 100%;
    max-height: 90vh;
    overflow: hidden;
}
.container .task .date{
    font-size: 7pt;
    color: #212529;
}